import React, { useEffect, useState } from 'react';
import axios from 'axios';
import {Link} from 'react-router-dom';

function PostDetail({ match, category }) {

    const [post, setPost] = useState({})
    const id = match.params.id

    useEffect(() => {
        axios({
            method: "GET",
            url: `http://worker.std-1378.ist.mospolytech.ru/api/blogpost/${id}/`
        }).then(response => {
            setPost(response.data)
        })
    }, [id])

    return (
        <div>
            { category != undefined ? <Link to={{pathname:`/category/${category}`, fromDashboard: false}}>Назад</Link>:null}
            <p>Название: <strong>{post.title}</strong></p>
            <p>Зарплата<strong>{post.salary}</strong></p>
            {post.owner !== undefined ? <p>Создатель:{post.owner.email}</p> : null}
            <p>Описание</p>
            <p>{post.content}</p>
            <a href={post.link} rel='noreferrer' target='_blank'>Ссылка для связи</a>
            <h2>Комментарии</h2>
            {console.log(post.comments)}
            {post.comments.length()!=0 ? post.comments.map((unit) => {
                return(
                <div key={unit.id}>
                    <span>Написал: {unit.owner}</span>
                    <span>{unit.comment}</span>
                </div>)
            })
            :null
            }
            <Link to={{pathname:`/comment/${id}`, fromDashboard: false}}>Добавить</Link>
        </div>
    )
}

export default PostDetail